export default function SeccionTextoImagen({ data }: { data: any }) {
  const esImagenDerecha = data.posicion_image === "derecha";
  const baseUrl = process.env.NEXT_PUBLIC_STRAPI_URL;

  return (
    <section className="py-16 bg-white overflow-hidden">
      <div className={`container mx-auto px-6 flex flex-col md:items-center gap-12 ${esImagenDerecha ? 'md:flex-row-reverse' : 'md:flex-row'}`}>
        
        {/* Contenedor de Imagen */}
        <div className="flex-1">
          {data.imagen?.url && (
            <img 
              src={`${baseUrl}${data.imagen.url}`} 
              alt={data.titulo}
              className="w-full h-auto rounded-2xl shadow-xl object-cover"
            />
          )}
        </div>

        {/* Contenedor de Texto */}
        <div className="flex-1 space-y-6">
          <h2 className="text-4xl font-bold text-gray-900">
            {data.titulo}
          </h2>
          <div className="text-gray-600 text-lg leading-relaxed">
            {/* Aquí asumimos que es texto simple, si usas Blocks necesitas un renderizador */}
            <p>{data.descripcion}</p>
          </div>
        </div>

      </div>
    </section>
  );
}