export default function Slider({ Slides }: any) {
  return (
    <section className="py-16 bg-white">
      <div className="container mx-auto px-4 flex overflow-x-auto gap-8 pb-10 scrollbar-hide">
        {Slides?.map((slide: any, index: number) => {
          // Extraemos la URL de la imagen si Strapi la envía
          const imgUrl = slide.imagen?.url 
            ? `${process.env.NEXT_PUBLIC_STRAPI_URL}${slide.imagen.url}` 
            : null;

          const textoDescripcion = slide.descripcion?.[0]?.children?.[0]?.text || "";
          
          // Extraemos la URL y label del botón del slide
          const textoBoton = slide.boton?.[0]?.label || "Leer más";
          const enlaceBoton = slide.boton?.[0]?.URL || "#"; 

          return (
            <div key={index} className="min-w-[350px] bg-gray-50 rounded-3xl shadow-md border border-gray-100 overflow-hidden">
              <div className="h-64 bg-gray-200">
                {imgUrl ? (
                  <img src={imgUrl} className="w-full h-full object-cover" alt={slide.titulo} />
                ) : (
                  <div className="flex items-center justify-center h-full text-gray-400 font-medium italic">
                    Sin imagen
                  </div>
                )}
              </div>
              <div className="p-8">
                <h3 className="text-2xl font-bold mb-3">{slide.titulo}</h3>
                <p className="text-gray-600 mb-6 line-clamp-2">{textoDescripcion}</p>
                <a 
                  href={enlaceBoton} 
                  target="_blank" 
                  rel="noopener noreferrer"
                  className="text-blue-600 font-bold hover:text-blue-800 transition-colors"
                >
                  {textoBoton} →
                </a>
              </div>
            </div>
          );
        })}
      </div>
    </section>
  );
}